<template>
  <div  class="form-rowbox" style="padding:20px 0;">
    <div class="inputbox">
      <span class="label"></span>
      <input ref="pwd" type="password" v-model="password" name="password" placeholder="" class="eyecheck">
      <eyes :dom="$refs.pwd"></eyes>
    </div>
    <div class="inputbox">
      <span class="label"></span>
      <input ref="pwd1" type="password" v-model="new_password" name="new_password"
             placeholder="6-16">
      <eyes :dom="$refs.pwd1"></eyes>
    </div>
    <div class="inputbox">
      <span class="label"></span>
      <input ref="pwd2" type="password" v-model="confirm_password" name="confirm_password" placeholder=""
             class="eyecheck">
      <eyes :dom="$refs.pwd2"></eyes>
    </div>
    <button type="submit" class="formbtn btn-submit" @click="submit"></button>

  </div>
</template>
<script>
  import md5 from "MD5";
  import {changepws} from "api/payment";
  import eyes from "components/eyes"
  import  check from "@/util/RegExp"
  export default {
    data() {
      return {
        password: "",// true string
        confirm_password: "",// true string
        new_password: ""
      };
    },
    props: {},
    methods: {
      check() {
        if(this.password == "") toast("");
        else if (!check.password.test(this.new_password))toast("6-16");
        else if (this.new_password == "") toast("");
        else if (this.confirm_password == "") toast("");
        else if (this.new_password != this.confirm_password) toast("");
        else if (this.password == this.new_password) toast("");
        else {
          return true;
        }
      },
      submit() {
        if (!this.check()) return;
        changepws(
          this.$data
        ).then(data => {
          if (data.success) {
            toast(data.message);
            this.password = this.confirm_password = this.new_password = ""
          } else {
            toast(data.message)
          }
        }).catch(err => {
          toast(",")
        })
      }
    },
    components:{eyes}
  };
</script>
<style lang="scss">
</style>



