<template>
  <div class="form-rowbox perfect-data">
    <div class="inputbox">
      <span class="label"></span>
      <input type="text" v-model="Data.accountName" placeholder=""/>
    </div>
    <div class="inputbox">
      <span class="label"></span>
      <input type="text" v-model="userData.phone" readonly placeholder=""/>
    </div>
    <div class="inputbox">
      <span class="label"></span>
      <input type="text" v-model="Data.email" placeholder="" value=""/>
    </div>
    <div class="inputbox">
      <span class="label"></span>
      <datepicker class="input" v-model="Data.birthday"></datepicker>
      <!--<input type="date" v-model="Data.birthday" placeholder="" class="inpt Wdate"/>-->
    </div>
    <div class="inputbox">
      <span class="label"></span>
      <input type="text" v-model="Data.bankno" @input="verifyBankNumber(Data.bankno)" placeholder=""/>
    </div>
    <div v-show="banktxt!=''" class="inputbox txt-bankname">
      <span style="white-space:nowrap">{{banktxt}}</span>
    </div>
    <div class="inputbox">
      <span class="label"></span>
      <input type="text" v-model="Data.wechat" placeholder=""/>
    </div>
    <div class="inputbox">
      <span class="label">Q Q</span>
      <input type="text" v-model="Data.qq" placeholder=""/>
    </div>
    <div>
      <a href="javascript:void(0);" class="formbtn" @click="submit"></a>
    </div>
  </div>
</template>
<script>
  import {completeUserInfo} from "api/user"
  import {validateBankNo} from "api/payment"
  import datepicker from "base/datepicker";
  import {mapGetters} from 'vuex'
  import check from "@/util/RegExp"
  import {bank} from "@/vue-extend/mixins/bank"
  export default {
    mixins:[bank],
    data() {
      return {
        Data: {
          birthday: "", //true string
          accountName: "", //true string
          bankno: "", //true string
          email: "", //true string
          qq: "",
          wechat: ""
        }
      }
    },
    methods: {
      checked() {
        if (this.Data.accountName == "") toast("");
        else if (!check.name.test(this.Data.accountName)) toast("");
        else if (this.Data.email != "" && !check.email.test(this.Data.email)) {
          toast("")
        }
        else if (this.Data.birthday == "") toast("");
        else if (this.Data.bankno == "") toast("");
        else if (!this.validateBankNo) toast("");
        else {
          return true;
        }
      },
      submit() {
        if (!this.checked()) return;
        completeUserInfo(this.Data).then(data => {
          if (data.success) {
            this.$store.dispatch("UPDATE_USERDATA");
            $confirm("!<br>ip", "", {
              confirmText: ""
            }).then(() => {
              this.$router.push("/user/discount/experience");
            });
          } else {
            toast(data.message)
          }
        }).catch(err => {
          toast("");
        })
      }
    },
    created() {
    },
    computed: {
      ...mapGetters(["userData"])
    },
    components: {
      datepicker
    }
  }
</script>
<style lang="scss" rel="stylesheet/scss">
  .perfect-data {
    margin: 20px;
    border: solid 1px #ccc;
    padding: 20px 0;
  }
  .form-rowbox .txt-bankname{
    font-size: 14px;
    padding-left: 112px;
    color: #FF1E1E;
  }
</style>



