<template>
  <div class="form-rowbox" style="padding: 20px 0;">
    <div class="inputbox">
      <span class="label"></span>
      <input ref="pwd" type="password" v-model="originalPwd" name="originalPwd" placeholder="" class="eyecheck" maxlength="6">
      <eyes :dom="$refs.pwd"></eyes>
    </div>
    <div class="inputbox">
      <span class="label"></span>
      <input ref="pwd1" type="password" v-model="newPwd" name="newPwd" placeholder="6" class="eyecheck" maxlength="6">
      <eyes :dom="$refs.pwd1"></eyes>
    </div>
    <button type="submit" class="formbtn btn-submit" @click="submit"></button>
  </div>
</template>
<script>
  import  {modifyWithdrawPwd}  from "api/payment";
  import  md5  from "MD5";
  import eyes from "components/eyes"
  import  check from "@/util/RegExp"
  export default {
    data() {
      return {
        originalPwd:"",
        newPwd:""
      };
    },
    methods:{
      checkFrom(){
        if(this.originalPwd=='')toast("");
        else if(!check.pay_password.test(this.newPwd))toast("6");
        else if(this.newPwd=='')toast("");
        else if(this.newPwd==this.originalPwd)toast("");
        else{
          return true;
        }
      },
      submit(){
        if(!this.checkFrom())return;
        modifyWithdrawPwd({
          originalPwd:md5(md5(this.originalPwd)),
          newPwd:md5(md5(this.newPwd))
        }).then(data=>{
          if(data.success){
            this.originalPwd=this.newPwd='';
            toast("");

          }else{
            toast(data.message);
          }
        }).catch(err=>{
          toast(",");
        })
      }
    },
    components:{eyes}
  };
</script>
<style>
</style>



