<template>
  <div class="transfer-info">

    <div class="orangebox wt">
      <div class="inputbox">
        <span class="label"></span>
        <div class="styled-select">
          <select v-model="type">
            <option value="0"></option>
            <option value="1"></option>
          </select>
        </div>
      </div>
      <div v-show="type==0">
        <div class="inputbox">
          <span class="label"></span>
          <input type="text" readonly :value="userData.accountMoney">
        </div>
        <div class="inputbox">
          <span class="label"></span>
          <select ref="transOut" v-model="data.transferGameOut">
            <option value=""></option>
            <option v-for="item  in platformData" :value="item.value">
              {{item.name}}
            </option>
          </select>
        </div>
      </div>
      <div v-show="type==1">
        <div class="inputbox">
          <span class="label"></span>
          <select v-model="data.transferGameIn" ref="indom">
            <option value=""></option>
            <option v-for="item  in platformData" :value="item.value">
              {{item.name}}
            </option>
          </select>
        </div>
        <div class="inputbox">
          <span class="label"></span>
          <select v-model="data.transferGameOut">
            <option value="MAIN" selected="selected"> ({{userData.accountMoney}})</option>
            <option value="DEPUTY"> ({{userData.deputyCredit}})</option>
          </select>
        </div>
      </div>
      <div class="inputbox">
        <span class="label"> </span>
        <input type="text" v-model="data.transferGameMoney" placeholder="">
      </div>
      <a class="formbtn" @click="transfer"></a>
    </div>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'
  import {updateGameMoney} from "api/payment"
  import {getGameMoney} from "api/user"
  import {platformData} from "@/assets/data"

  export default {
    data() {
      return {
        platformData: platformData.concat([{name: "AG", value: "AGIN"}, {name: "EBET", value: "EBET_LIVE"}, { name: "", value: "SB" }]),
        type: 0,//0  1
        outMoney: '',
        data: {
          transferGameOut: "", //
          transferGameMoney: "",
          transferGameIn: "MAIN"//
        }
      };
    },
    watch: {
      "data.transferGameMoney"(val) {
        if (/[^\d]/g.test(val)) {
          this.data.transferGameMoney = this.data.transferGameMoney.toString().replace(/[^\d]/g, '')
        }
      },
      type(val) {
        if (val == 0) {
          this.data.transferGameIn = "MAIN"
          this.data.transferGameOut = ""

        } else {
          this.data.transferGameIn = "";
          this.data.transferGameOut = "MAIN"
        }
      },
      "data.transferGameOut"(val) {
        this.outMoney="";
        if (this.type == 0) {
          if (val == "") return;
          this.getGameMoney(val, this.$refs.transOut, 0)
        } else {
          this.outMoney = (val == 'MAIN' ? this.userData.accountMoney : this.userData.deputyCredit);
          if (val == 'DEPUTY' && (this.data.transferGameIn == 'EBET_LIVE' || this.data.transferGameIn == 'AGIN')) {
            $alert("AGEBTE");
            this.data.transferGameOut = "MAIN"
          }
        }
      },
      "data.transferGameIn"(val) {
        if (this.data.transferGameOut == 'DEPUTY' && (val == 'EBET_LIVE' || val == 'AGIN')) {
          this.data.transferGameIn = ""
          $alert("AGEBTE");
        }
        if (this.type == 1 && val != '')
          this.getGameMoney(val, this.$refs.indom,1)
      }
    },
    computed: {
      ...mapGetters(["userData"])
    },
    methods: {
      transfer() {
        if (this.outMoney!== '' && this.data.transferGameMoney * 1 > this.outMoney * 1) {
          return $alert("");
        }
        if (this.data.transferGameMoney == "") return $alert("");
        else if (this.data.transferGameIn == "") return $alert(`${this.type == 0 ? "" : ""}`);
        else if (this.data.transferGameOut == "") return $alert(`${this.type == 0 ? "" : ""}`);
        updateGameMoney(this.data).then(res => {
          $alert(res.message);
          if (res.success) {
            this.$store.dispatch("UPDATE_USERDATA");
            if (this.type == 0) {
              this.getGameMoney(this.data.transferGameOut, this.$refs.transOut, 0)
            } else if (this.type == 1) {
              this.getGameMoney(this.data.transferGameIn, this.$refs.indom, 1)
            }
          }
        }).catch(err => {
          toast("");
        })
      },
      getGameMoney(val, dom, type) {
        let options = dom.options,
          curIndex = options.selectedIndex;
        options[curIndex].text = this.platformData[options.selectedIndex - 1].name + "(...)";
        getGameMoney({gameCode: val}).then(res => {
          if (res.success) {
            if (this.type == 0) {
              this.outMoney = res.data;
            }
            options[curIndex].text = this.platformData[curIndex - 1].name + " (" + res.data + ")";
          } else {
            options[curIndex].text = this.platformData[curIndex - 1].name + " (" + res.message + ")";
            ;
          }
        }).catch(err => {
          options[curIndex].text = this.platformData[curIndex - 1].name + "()";
        })
      }
    }
  }
</script>
<style lang="scss">
  .transfer-info {
    box-sizing: border-box;
    padding: 50px 30px 30px;
  }
</style>



