import store from '@/store';	//
import {checkLogin} from 'api/authService';
import {AUTH_NAME} from "@/store/types"//
// 
export default (to,from,next) =>{
  // return next()
  let needFalseLogin=to.matched.some(function(item,i){
    return item.meta.needFalseLogin===true;
  })
  let NEED_AUTH=to.matched.some(function(item,i){
    return item.meta.auth;
  }) //
  if(needFalseLogin){ //
    if(store.getters.isAgent){
      next({name:"agent_index"})
    }else{
      if(!store.getters.islogin){//
        next();
      }else{  // index  
        checkLogin().then(res =>{  //  / 
          if(res.success){
            let {role} = res.data;
            if(role==AUTH_NAME.USER){//
              next({name:'user_index'});
            }
            else if(role==AUTH_NAME.AGENT){ // 
              next({name:'agent_index'});
            }else{ //
              next({path:'/index',query:{rquest:to.fullPath}});
            }
          }else{
            next()
          }
        }).catch(err =>{
          next("/");
        })
      }
    }
  }
  else if(NEED_AUTH){ //

    let USER_AUTH=to.matched.some(function(item,i){  //
      return Array.isArray(item.meta.auth)&&item.meta.auth.includes(AUTH_NAME.USER);
    })
    let AGENT_AUTH=to.matched.some(function(item,i){  //
      return Array.isArray(item.meta.auth)&&item.meta.auth.includes(AUTH_NAME.AGENT);
    })

    if(!store.getters.islogin){ //
      checkLogin().then(res =>{
          if(res.success){
            let {role} = res.data;
            store.commit("SET_AUTH",role);
            store.commit("SET_USERDATA",res.data);
            if(AGENT_AUTH&&USER_AUTH){ //
              next();
            }
            else if(USER_AUTH&&role==AUTH_NAME.USER){//
              next();
            }
            else if(AGENT_AUTH&&role==AUTH_NAME.AGENT){ // 
              next();
            }else{ //
              next({path:'/index',query:{rquest:to.fullPath}});
            }
            //
          }else{ //

            store.dispatch("INIT_INFO");
            next({path:'/',query:{rquest:to.fullPath}});
          }
        }
      ).catch(() =>{
        store.dispatch("INIT_INFO");//
        next({path:'/',query:{rquest:to.fullPath}});
      });
    }else{
      if(USER_AUTH&&AGENT_AUTH){
        next();
      }
      else if(USER_AUTH&&store.getters.isUser){
        next();
      }
      else if(AGENT_AUTH&&store.getters.isAgent){ // 
        next();
      }else{//
        next({path:'/index',query:{rquest:to.fullPath}});
      }
    }
  }
  else{
    next();
  }
}



