
// Date Date String
// (M)(d)(h)(m)(s)(q)  1-2 
// (y) 1-4 (S) 1 ( 1-3 )
// 
// (new Date()).Format("yyyy-MM-dd hh:mm:ss.S") ==> 2006-07-02 08:09:04.423
// (new Date()).Format("yyyy-M-d h:m:s.S")      ==> 2006-7-2 8:9:4.18
Date.prototype.format = function (fmt='yyyy-MM-dd') { //author: meizz
  var o = {
    "M+": this.getMonth() + 1, //
    "d+": this.getDate(), //
    "h+": this.getHours(), //
    "m+": this.getMinutes(), //
    "s+": this.getSeconds(), //
    "q+": Math.floor((this.getMonth() + 3) / 3), //
    "S": this.getMilliseconds() //
  };
  if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
  for (var k in o)
    if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
  return fmt;
}
Date.prototype.addDay=function(days){
  return  new Date(this.getTime()+days*86400*1000);
}
if (!Date.prototype.getWeekNumber) {
  Date.prototype.getWeekNumber = function(){
    var d = new Date(+this);
    d.setHours(0,0,0,0);
    d.setDate(d.getDate()+4-(d.getDay()||7));
    return Math.ceil((((d-new Date(d.getFullYear(),0,1))/8.64e7)+1)/7);
  };
}
// 
String.prototype.$toFixed=function(_number=2){
  let _val= parseFloat(this).toString();
  _val=isNaN(_val)?"0":_val;
  _val+=_val.indexOf(".")>-1?"00":".00";
  return _val.replace(new RegExp('(\\d*\\.\\d{'+_number+'})\\d*','g'),"$1")
}

//
String.prototype.$copy=function(e){
  let $this=this;
  var save=function(e){
    e.clipboardData.setData('text/plain',$this);
    e.preventDefault();
  }
  document.addEventListener('copy',save);
  document.execCommand('copy');
  document.removeEventListener('copy',save);
  toast('');
}
//
String.prototype.toDate=function(){
  if(/(\-|\/)/g.test(this)){
    return new Date(this.replace(/-/g,"/"));
  }else if(/^\d+$/g.test(this)){
    return new Date(Number(this));
  }else{
    let arr='';
    if(new Date(this)=="Invalid Date"){
      arr=this.toString().replace(/[\:\-\/]/,",")
    }
    return new Date(arr);
  }
}

//  123456789 12,345,678
String.prototype.strSplit=function(num=3,rpl=","){
  return this.toString().replace(new RegExp("(\\w)(?=(\\w{"+num+"})+(?!\\w))",'g'), "$1"+rpl)
}

export const DateFormat=Date.prototype.format




