<template>
  <div class="drawmoney">
    <!-- step1 -->
    <div class="orangebox wt" v-show="step==1">
      <div class="transact_top">
        <h1 class="col1"><span class="">{{userData.accountMoney}}</span></h1>
      </div>
      <form action="" id="form-withdraw">
        <div class="inputbox">
          <span class="label"></span>
          <input v-model="sumitData.amount" type="text" placeholder="100">
        </div>
        <div class="inputbox">
          <span class="label"></span>
          <select v-model="sumitData.id">
            <option value=""></option>
            <option v-for="item in bankList" :value="item.id">{{item.bankname}}({{item.bankno}})</option>
          </select>
        </div>

        <div class="inputbox">
          <span class="label"></span>
          <input type="password" v-model="sumitData.password" name="password" placeholder="6">
          <a href="javascript:;" id="pwds1" class="showpwd eyeclose"></a>
        </div>
        <div class="clear"></div>
        <input type="button" @click="submit" class="formbtn" value="">
      </form>
    </div>
    <div class="orangebox wt" v-show="step==3">
      <p class="txt1"><span></span> <span class="cl-red">{{sumitData.amount}}</span></p>
      <p class="txt1"><span class="" style="line-height: 1.5;"><br>1</span>
      </p>
      <router-link :to="{name:'user_log',params:{type:'withdraw'}}" class="formbtn">
        
      </router-link>
    </div>
  </div>
</template>
<script>
  import md5 from "MD5";
  import {findUserBankList, checkWithdrawPwd, withdraw_new} from "api/payment";
  import {mapGetters} from 'vuex'

  export default {
    data() {
      return {
        step: 1,
        curBank: {},
        bankList: [],
        sumitData: {
          amount: "",
          id: "",
          password: "",
          tkType: ""// false string slotmachine-liveall-
        }
      };
    },
    watch: {},
    methods: {
      checkForm() {
        if(!this.isSetPayPwd) {
          $confirm("!", "", {
            confirmText: ""
          }).then(() => {
            this.$router.push({name: 'user_security'});
          })
        }
        else if (this.sumitData.amount == "") toast("!");
        else if (this.sumitData.amount <100) toast("100!");
        else if (this.sumitData.amount > this.userData.accountMoney) toast("!");
        else if (this.sumitData.id == "") toast("!");
        else if (this.sumitData.password == "") toast("!");
        else {
          return true;
        }
      },
      submit() {

        if(!this.checkForm()) return;
        this.sumitData.tkType = this.isAgent ? "slotmachine" : "";
        this.sumitData.password = md5(md5(this.sumitData.password))
        withdraw_new(this.sumitData).then(data => {
          this.sumitData.password = '';
          if (data.success) {
            this.$store.dispatch("UPDATE_USERDATA");
            this.step = 3;
          }
          toast(data.message);
        }).catch(err => {
          toast(",!")
        })
      }
    },
    computed: {
      ...mapGetters(["userData", 'isSetPayPwd', 'isAgent'])
    },
    created() {
      if(this.userData.accountName == "") {
        $confirm("!", "", {
          confirmText: ""
        }).then(() => {
          this.$router.push({name: 'user_datum'});
        })
        return
      }
      findUserBankList().then(res => {
        if (res.success) {
          this.bankList = res.data;
        }
        else {
          toast(res.message)
        }
      }).catch(err => {
        toast("")
      })
    },
    components: {}
  };
</script>
<style lang="scss">

  .drawmoney {
    box-sizing: border-box;
    padding: 50px 30px 30px;
    .orangebox.wt {
      margin-top: -5px;
      border-radius: 10px;
      .txt1 {
        padding-bottom: 20px;
        font-size: 16px;
        color: #1e327e;
        text-align: center;
        width: 100%;
        .cl-red {
          color: #f00;
        }
      }
    }
    .col1 {
      color: #333;
      font-size: 24px;
      font-weight: lighter;
      text-align: center;
      .col2 {
        color: #1e327e;
        font-size: 24px;
      }
    }
    .transact_top {
      height: 70px;
    }
  }
</style>



