      min-height: 270px;
      .mc_wx_h_tips {
        color: #333;
        margin: 20px 0;
        .mc_wx_red {
          color: red;
          font-weight: bold;
        }
      }
      img {
        height: 100%;
      }
    }
    .mc_wx_h_btns {
      text-align: center;
      .formbtn {
        display: inline-block;
        width: 240px;
        margin: 30px 20px;
      }
    }
    .mc_wx_control {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      cursor: pointer;
      transition: all .4s;

      &:hover {
        opacity: 0.8;
      }
      &.left {
        left: 0;
      }
      &.right {
        right: 0;
      }
    }

  }

</style>



