<template>
  <div class="discount-code">
    <div class="form">
      <div class="title-tip">
        
        <br>
      </div>
      <div class="inputbox">
        <label class="label"></label>
        <select v-model="data.couponType">
          <option value="">------</option>
          <option v-for="item in platformData" :value="item.value">{{item.name}}</option>
        </select>
      </div>
      <div class="inputbox">
        <label class="label"></label>
        <input type="text"
               v-model="data.couponRemit"
               placeholder=""
               maxlength="10">
      </div>
      <div class="inputbox">
        <label class="label"></label>
        <input type="text" v-model="data.couponCode" placeholder="" name="amount" maxlength="20">
      </div>
      <div>
        <a href="javascript:void(0);" @click="submit" class="formbtn"></a>
      </div>
    </div>
  </div>
</template>
<script>
  import {transferInforCoupon} from "api/preferential-terms"
  import {platformData} from "@/assets/data"

  export default {
    data() {
      return {
        platformData,
        data: {
          couponType: "", // true string
          couponRemit: "", //true string
          couponCode: "",//true string
        }
      };
    },
    props: {},
    watch: {
      "data.couponRemit"(val) {
        if (/[^\d]/g.test(val)) {
          this.data.couponRemit = this.data.couponRemit.toString().replace(/[^\d]/g, '')
        }
      }
    },
    methods: {
      check() {
        if (this.data.couponType == '') toast('');
        else if (!/^\d+$/.test(this.data.couponRemit)) toast('');
        else if (this.data.couponCode == '') toast('');
        else {
          return true;
        }
      },
      submit() {
        if (!this.check()) return;
        transferInforCoupon(this.data).then((data) => {
          if (data.success) {
            this.data.couponCode = '';
          }
          toast(data.message);
        }).catch(() => {
          toast('');
        })
      }
    },
    created() {
    },
    components: {}
  };
</script>

<style lang="scss">
  .discount-code {

  }
</style>



