import Vue from 'vue'; //vue
import Vuex from 'vuex'; //vue
import router from '@/router'; //vue
import {ajaxGetSessionPersonalData} from 'api/user'; //mutations
import {checkWithdrawPwd} from 'api/payment'; //mutations
import {logOut} from 'api/authService'; //mutations
import * as types from './types'; //vue
import {$localStorage,$sessionStorage} from '@/util/storage';
Vue.use(Vuex);
export default new Vuex.Store({
  state:{ //state
    showFooter:true,
    authList:[], //
    isSetPayPwd:null, //
    showLogin:false, //
    userData:{
      wechat:"",//
      qq:"",//true string
      loginname:"",// true string
      accountName:"",//t true string 
      phone:"",//true string//
      level:0,//true string 
      email:"",// false string 
      accountMoney:0,// true string 
      role:"",//true string 
      birthday:"",//false string 
      levelNumber:"",//true number 
      deputyCredit:"",//true number 
      phoneValidStatus:"",//true string 
      mobileNum:"",//true string 
      referWebsite:"",//
      liveAccount:0,//
      slotAccount:0,//
    }
  },
  getters:{
    showFooter(state,getters){
      Vue.nextTick(()=>{
        document.querySelector("body").style.paddingBottom=(state.showFooter?"":"0");
      })
      return state.showFooter;
    },
    showLogin(state,getters){
      return state.showLogin;
    },
    isSetPayPwd(state,getters){
      if(state.isSetPayPwd==null){ //
        checkWithdrawPwd().then(res=>{
          if(res.success){
            state.isSetPayPwd=res.data=='1';
          }
        })
      }
      return state.isSetPayPwd;
    },
    userData(state,getters){ //
      return state.userData;
    },
    authList(state,getters){  //
      return state.authList;
    },
    isAgent(state,getters){ //
      return state.authList.includes(types.AUTH_NAME.AGENT);
    },
    isUser(state,getters){ //
      return state.authList.includes(types.AUTH_NAME.USER);
    },
    islogin(state,getters){ //  //===true
      if($sessionStorage&&$sessionStorage.get(types.IS_LOGIN)==='true'){ //sessionStorage
        if(state.authList.length==0){ // authList 
          return false
        }
        return true;
      }
      if(state.authList.length>0){  //sessionStorage
        $sessionStorage&&$sessionStorage.set(types.IS_LOGIN,true)
        return true;
      }
      return false;
    }
  },
  mutations:{
    [types.SET_FOOTER](state,val){
      state.showFooter=val
    },
    [types.SET_PAYPWD](state,val){
      state.isSetPayPwd=val
    },
    [types.SET_USERDATA](state,val={}){
      Object.assign(state.userData,val||{});
    },
    [types.SHOW_LOGIN](state,val){
      state.showLogin=val;
    },
    [types.SET_AUTH](state,val){ //
      if(typeof val==='string'){
        state.authList=[val];
      }
      else if(Array.isArray(val)){
        state.authList=val;
      }else{
        state.authList=[];
      }
    },
    [types.ADD_AUTH](state,val){
      state.authList.push(val);
    },
    [types.REMOVE_AUTH](state,val){
      if(val===undefined){ //
        $sessionStorage&&$sessionStorage.remove(types.IS_LOGIN);
        state.authList=[]
      }else{
        let index=state.authList.indexOf(val)
        if(index> -1){
          state.authList.splice(index,1);
        }
      }
    },

  },
  actions:{
    [types.INIT_INFO]({commit},val){ // 
      commit(types.REMOVE_AUTH);
      commit(types.SET_PAYPWD,null)
      commit(types.SET_USERDATA,{
        qq:"",//true string
        loginname:"",// true string
        accountName:"",//t true string 
        phone:"",//true string
        wechat:"",//
        level:0,//true string 
        email:"",// false string
        accountMoney:0,// true string
        role:"",//true string 
        birthday:"",//false string 
        levelNumber:"",//true number 
        deputyCredit:"",//true number 
        phoneValidStatus:"0",//true string 
        mobileNum:"",//true string 
        referWebsite:"",//
        liveAccount:0,//
        slotAccount:0,//
      })
    },
    [types.LOGIN_OUT]({commit,dispatch},val){ //
      dispatch(types.INIT_INFO)
      logOut().then(res=>{
        router.push("/")
      }).catch(res=>{
        toast("")
      })
    },
    [types.GET_PAYPWD]({commit},val){  // 
      return new Promise((resolve,reject)=>{
        checkWithdrawPwd().then(res=>{
          if(res.success){
            commit(types.SET_PAYPWD,res.data=="1");
          }
          return resolve(res)
        }).catch(err=>{
          reject(err)
        })
      })
    },
    [types.UPDATE_USERDATA]({commit,dispatch},val){ // 
      return ajaxGetSessionPersonalData().then(res =>{
        if(res.success){
          dispatch(types.GET_PAYPWD)
          commit(types.SET_USERDATA,res.data);
          commit(types.SET_AUTH,res.data.role);
        }
        return Promise.resolve(res);
      });
    }
  }
});



