<template>
  <div class="experience-gold">
    <div class="guide-page" v-show="step==0">
       <h2 class="guide-tip">APP</h2>
       <img src="./images/app_download.png" width="453">
    </div>
    <div v-show="step==1">
      <h3 class="title-tip">{{limitAmount}}</h3>
      <br>
      <button  :disabled="!success"  v-if="userData.accountName!=''"   :class="['formbtn',{disabled:!success}]"   @click="getMoney">{{msg}}</button>
      <router-link v-else class="formbtn"  :to="{name:'user_datum'}"></router-link>
    </div>
    <div class="orangebox " v-show="step==2">
      <!--<h3 class="title-tip"><span>8</span></h3>-->
      <p>TTGNT<br></p>
      <br>
      <div class="inputbox">
        <span class="label"></span>
        <select v-model="platform">
          <option value=""></option>
          <option v-for="item in platformData" :value="item.value">
            {{item.name}}
          </option>
        </select>
      </div>
      <br>
      <a href="javascript:;" @click="submit" class="formbtn cmargin"></a>
    </div>
  </div>
</template>
<script>
  import {commitPT8Self, canGetPt8CouponCheck,youhuiDisplay} from "api/preferential-terms"
  import {platformData} from "@/assets/data"
  import {mapGetters} from 'vuex'

  export default {
    data() {
      return {
        success:false, //
        msg: "",
        limitAmount:0,
        money: "0",
        step: 0,       // 0  APP 
        platformData,
        platform: ""
      };
    },
    methods: {
      getMoney(){
        if(this.success){
          this.step=2;
        }
        else{
          toast(this.msg)
        }
      },
      submit() {
        if (this.platform == "") return toast("")
        if (this.userData.accountName == "") {
          $confirm("!", "", {
            confirmText: ""
          }).then(() => {
            this.$router.push({name:'user_datum'});
          })
          return
        }
        else if (this.userData.phoneValidStatus==0){
          $confirm("!", "", {
            confirmText: ""
          }).then(() => {
            this.$router.push({name:'user_datum'});
          })
          return
        }

        commitPT8Self({platform: this.platform}).then(res => {
          if (res.success) {
            $confirm("!", "", {
              confirmText: ""
            }).then(() => {
              this.$router.push({name:'slotgame'});
            })
            this.step = 1;
          }
          else {
            toast(res.message);
          }
        }).catch(err => {
          toast("");
        })
      }
    },
    computed: {
      ...mapGetters(["userData"])
    },
    created() {
      youhuiDisplay({'youhuiType':'pt8'}).then(res=>{
        if(res.success){
          this.limitAmount=res.data.lowestAmount
          this.money = res.data.amount
        }
      })
      canGetPt8CouponCheck().then(res => {
        this.success=res.success;
        this.msg = res.message
        if (res.success) {
          this.money = res.data.amount;
          this.limitAmount=res.data.limitAmount;
        }
      })
    },
    components: {}
  };
</script>
<style lang="scss">
  .experience-gold{
     p {
      color: #1e327e;
      text-align: center;
      width: 100%;
      font-size: 24px;
    }
    
    .guide-page{
      text-align: center;
      .guide-tip{
        color: #f00;
        font-size: 16px;
        font-weight: normal;
        line-height: 30px;
        margin-bottom: 15px;
      }
      img{
        margin-left: 60px;
      }
    }

  }

</style>



